.cocktails-list__img {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 25px;
  transition: all 1s;
}

.cocktails-list__info-box {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background-color: #faaca8;
  background-image: linear-gradient(19deg, #faaca8 0%, #ddd6f3 100%);
  border-radius: 25px;
  padding: 30px 20px;
  transition: all 1s;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateY(180deg);
}

.cocktails-list__item:hover .cocktails-list__img {
  transform: rotateY(180deg);
}

.cocktails-list__item:hover .cocktails-list__info-box {
  transform: rotateY(360deg);
}

.cocktails-list__item:hover .cocktails-list__fact-question {
  animation: tracking-in-expand-fwd 1.5s cubic-bezier(.215, .61, .355, 1) 2s both;
}

